import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class DecisionService {
  public options: Object;
  // public apiUrl:string = 'http://192.168.1.227:8009/api/GP2';
  public apiUrl:string = '/api/GP2'
  constructor(
    protected http: HttpClient,
    protected headers:HttpHeaders=null,
  ) {
    if(headers==null){
      headers = new HttpHeaders({
        'Content-Type': 'application/json; charset=utf-8',
        "token": window.localStorage.getItem("token")
        // application/x-www-form-urlencoded

      });
    }

    this.options = { headers: headers, responseType: 'json' };
  }

  // default ----- ----- ----- ----- ----- ----- ----- ----- ---------- ----- ----- ----- ----- ----- ----- ----- ---------- ----- ----- ----- ----- ----- ----- ----- ---------- ----- ----- ----- ----- ----- ----- ----- ---------- ----- ----- ----- ----- ----- ----- ----- ---------- ----- ----- ----- ----- ----- ----- ----- -----
  public get(query: string): Promise<any> {
    return new Promise<any>(resolve => {
      this.http.get(this.apiUrl + '' + query,this.options).subscribe(data => {
        resolve(data);
      }, error => {
        console.log(error);
        resolve(null);
      });
    });
  }

  protected delete(query: string): Promise<any> {
    return new Promise<any>(resolve => {
      this.http.delete(this.apiUrl+query, this.options).subscribe(data => {

        resolve(data);
      }, error => {
        console.log(error);
        resolve(null);
      });
    });
  }

  protected post(query: string, posts: any = null, options = null): Promise<any> {

    return new Promise<any>(resolve => {
      this.http.post(this.apiUrl + query,JSON.stringify(posts), this.options).subscribe(data => {
        resolve(data);
      });
    });
  }

  protected put(query: string, posts: any = null, options = null): Promise<any> {

    return new Promise<any>(resolve => {
      this.http.put(this.apiUrl + query,posts, this.options).subscribe(data => {
        resolve(data);
      });
    });
  }
}
