import { Component, OnInit, ElementRef, ViewChild, Renderer2} from '@angular/core';
import { animate, state, style, transition, trigger } from '@angular/animations';
import { DecisionDashboardService } from '../../service/service/decision/decision-dashboard.service'

@Component({
  selector: 'app-cost-comparision',
  templateUrl: './cost-comparision.component.html',
  styleUrls: ['./cost-comparision.component.css'],
  animations: [
    trigger('detailExpand', [
      state('void', style({ height: '0px', minHeight: '0', visibility: 'hidden' })),
      state('*', style({ height: '*', visibility: 'visible' })),
      transition('void <=> *', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ],
})
export class CostComparisionComponent implements OnInit {
  collegeSelectData;
  nextStep;
  step;
  awardsArr=[];
  costsArr=[];
  outlook = [];
  showArrow = {
    l:false,
    r:false,
  };
  colleges = [];
  @ViewChild('collegelist') canRoll: ElementRef;
  @ViewChild('collegebox') collegebox :ElementRef;
  constructor(private render:Renderer2,private api:DecisionDashboardService) { }

  ngOnInit() {
    this.api.getProjectedCost().then(res=>{
      this.collegeSelectData = res.CollegeChoices;
      this.awardsArr = res.CostCompareItems.slice(0,23);
      this.costsArr = res.CostCompareItems.slice(23,40);
      this.outlook = res.CostCompareItems.slice(40,65);
      console.log(this.awardsArr,this.costsArr,this.outlook)
    })
  }

  changeCollege(value){
    let awardsdataList=[];
    let costdataList =[];
    let outlookdataList =[];
    this.api.getCollegeCompareData(value.key).then(res=>{
      for(let i = 0;i<this.awardsArr.length;i++){
        for(let j =0;j<res.CostCompareItems.length;j++){
          if(this.awardsArr[i].InternalName == res.CostCompareItems[j].CostItemID){
            res.CostCompareItems[j].className =this.awardsArr[i].CSSStyle
            awardsdataList.push(res.CostCompareItems[j]);
          }
        }
      }
      for(let i = 0;i<this.costsArr.length;i++){
        for(let j =0;j<res.CostCompareItems.length;j++){
          if(this.costsArr[i].InternalName == res.CostCompareItems[j].CostItemID){
            res.CostCompareItems[j].className =this.costsArr[i].CSSStyle
            costdataList.push(res.CostCompareItems[j]);
          }
        }
      }
      for(let i = 0;i<this.outlook.length;i++){
        for(let j =0;j<res.CostCompareItems.length;j++){
          if(this.outlook[i].InternalName == res.CostCompareItems[j].CostItemID){
            res.CostCompareItems[j].className =this.outlook[i].CSSStyle
            outlookdataList.push(res.CostCompareItems[j]);
          }
        }
      }
      this.colleges.push({award:awardsdataList,cost:costdataList,outlook:outlookdataList,name:value.value})
      console.log(this.colleges)
      this.updataArrowStatus();
    })
    
  }

  updataArrowStatus(){
    let num = this.colleges.length;
    if(num > 4){
      let width = this.collegebox.nativeElement.offsetWidth;
      this.step = width;
      this.showArrow.r = this.canRoll.nativeElement.offsetLeft > this.step*(-1)*(num-4) ?  true : false ;
      this.canRoll.nativeElement.offsetLeft<0 ? this.showArrow.l = true : this.showArrow.l=false;
    }else{
      this.showArrow.r=this.showArrow.l=false;
    }
  }

  moving(i){
    let list2 = this.canRoll.nativeElement;
    let distance = list2.offsetLeft+i*this.step + 'px'
    this.render.setStyle(this.canRoll.nativeElement, 'left', distance);
    this.updataArrowStatus();
  }

  counter(i: any) {
    return new Array(i);
  }
}
