import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { SharedModule } from '../shared/shared.module';

import { DecisionComponent } from './decision.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { MyDecisionComponent } from './my-decision/my-decision.component';
import { CostComparisionComponent } from './cost-comparision/cost-comparision.component';
import { ResponsesTableComponent } from './my-decision/responses-table/responses-table.component';
import { AwardsTableComponent } from './my-decision/awards-table/awards-table.component';
import {CdkDetailRowDirective} from './cdk-detail-row.directive';

@NgModule({
  imports: [
    SharedModule,
    RouterModule.forChild([
      {
        path: '',
        component: DecisionComponent,
        children: [
          { path: '',component: DashboardComponent},
          { path: 'MyDecision',component: MyDecisionComponent},
          { path: 'CostComparision',component: CostComparisionComponent},
        ]
      }
    ]
    )
  ],
  declarations: [
    DashboardComponent,
    DecisionComponent,
    DashboardComponent,
    MyDecisionComponent,
    CostComparisionComponent,
    ResponsesTableComponent,
    AwardsTableComponent,
    CdkDetailRowDirective
  ]
})
export class DecisionModule { }
