import { Component, OnInit } from '@angular/core';
import { Chart } from 'angular-highcharts';
import { DecisionDashboardService } from '../../service/service/decision/decision-dashboard.service';
import jsPDF from 'jspdf';
import html2canvas from 'html2canvas';

@Component({
  selector: 'app-decision-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
  plansChart=new Chart();
  stackingChat=new Chart();
  Model;
  charFlag:boolean=false;
  constructor(private api:DecisionDashboardService) { }

  ngOnInit() {
    this.api.getDashBoard().then(res=>{
      this.Model = res;
      let xval=[],chartData=[];
      let stackingChatObj = {
        collegeNames:[],
        grants:[],
        scholarships:[],
        sLoans:[],
        pLoans:[],
        workStudy:[],
        studentEmployment:[],
        other:[],
        oop:[],
      }
      for(let key in res.MostRecentResponses){
        xval.push(key);
        chartData.push(res.MostRecentResponses[key].Count);
      }
      for(let key in res.CollegeFinAid){
        stackingChatObj.collegeNames.push(res.CollegeFinAid[key].CollegeName)
        stackingChatObj.grants.push(res.CollegeFinAid[key].Grant)
        stackingChatObj.scholarships.push(res.CollegeFinAid[key].Scholarship);
        stackingChatObj.sLoans.push(res.CollegeFinAid[key].StudentLoan);
        stackingChatObj.pLoans.push(res.CollegeFinAid[key].ParentLoan);
        stackingChatObj.workStudy.push(res.CollegeFinAid[key].WorkStudy);
        stackingChatObj.studentEmployment.push(res.CollegeFinAid[key].StudentEmployment);
        stackingChatObj.other.push(res.CollegeFinAid[key].OtherAward);
        stackingChatObj.oop.push(res.CollegeFinAid[key].OutOfPocket);
      }
      this.initChart({xval,chartData},stackingChatObj);
      this.tableData =  res.UpcomingResponses;
    })
  }

  tableColumns:String[] =['NotificationDate','College','ApplictionType'];
  tableData:Array<any>;

  initChart(plansChartObj,stackingChatObj){
    this.plansChart = new Chart({
      chart: {
          type: 'bar',
          marginBottom:50
      },
      title:{
        text:null
      },
      xAxis: {
          categories:plansChartObj.xval,
          title: {
              text: null
          },
          tickLength:0
      },
      yAxis: {
          min: 0,
          title: {
              text: 'Number of Appliction',
              align: 'high'
          },
          labels: {
              overflow: 'justify'
          },
          tickLength:0
      },
      tooltip: {
          valueSuffix: ''
      },
      legend:{
        enabled:false
      },
      colors:['#97c846'],
      plotOptions: {
          bar: {
            dataLabels: {
                enabled: true
                // ,allowOverlap 默认是 false，即不允许数据标签重叠
            },
            pointWidth:30
          },
          column: {
            colorByPoint:true
          }
      },
      credits: {
          enabled: false
      },
      series: [{
        data:plansChartObj.chartData
      }]
    })
    this.stackingChat =new Chart({
      chart: {
        type: 'bar'
    },
    title: {
        text: null
    },
    xAxis: {
        categories: stackingChatObj.collegeNames
    },
    yAxis: {
        min: 0,
        title: {
          text: null
      }
    },
    credits: {
      enabled: false
  },
    colors: [
      '#c23934', 
      '#04844b', 
      '#ffb75d'
      ],
    legend: {
        /* 图例显示顺序反转
         * 这是因为堆叠的顺序默认是反转的，可以设置 
         * yAxis.reversedStacks = false 来达到类似的效果 
         */
        reversed: true 
    },
    plotOptions: {
        series: {
            stacking: 'normal'
        },
        column: {
          colorByPoint: true
        },
        bar: {
          dataLabels: {
              enabled: true
              // ,allowOverlap 默认是 false，即不允许数据标签重叠
          },
        }
    },
    series: [
      {
        name: 'Grants',
        color: '#25ef0f',
        data:stackingChatObj.grants,
        index: 8
      }, {
          name: 'Scholarships',
          color: 'green',
          data: stackingChatObj.scholarships,
          index: 7
      }, {
          name: 'OtherAwards',
          color: 'yellow',
          data: stackingChatObj.other,
          index: 6
      },  {
          name: 'Work Study',
          color: 'blue',
          data: stackingChatObj.workStudy,
          index: 5
      },  {
          name: 'Student Employment',
          color: '#9999ff',
          data: stackingChatObj.studentEmployment,
          index: 4
      }, {
          name: 'Student Loans',
          color: '#ffd34f',
          data: stackingChatObj.sLoans,
          index: 3
      }, {
          name: 'Parent Loans',
          color: '#d37614',
          data: stackingChatObj.pLoans,
          index: 2
      }, {
          name: 'Out-of-Pocket',
          data: stackingChatObj.oop,
          color: 'red',
          index: 1
      }]
    // series: [{
    //     name: 'Scholarships',
    //     data: [10,1]
    // }, {
    //     name: 'Student Loans',
    //     data: [0,2]
    // }, {
    //     name: 'Out-of-Pocket',
    //     data: [0,4]
    // }]
    })
    this.charFlag =true;
    console.log(this.charFlag);
  }

  exportPdf(){
    var element = document.getElementById("page");
    var width = element.clientWidth;//dom宽
    var height = element.clientHeight;//dom高
    var scale = 2;//放大倍数
    var canvas = document.createElement('canvas');
    canvas.width = width*scale;//canvas宽度
    canvas.height = height*scale;//canvas高度
        html2canvas(element, {
            dpi: window.devicePixelRatio*2,
            scale:scale,
            canvas:canvas,
            width:width,
            heigth:height,
        }).then(function(canvas) {
            var pdf = new jsPDF('p', 'mm', 'a4');    //A4纸，纵向
            var ctx = canvas.getContext('2d'),
                a4w = 190, a4h = 277,    //A4大小，210mm x 297mm，四边各保留10mm的边距，显示区域190x277
                imgHeight = Math.floor(a4h * canvas.width / a4w),    //按A4显示比例换算一页图像的像素高度
                renderedHeight = 0;

            while(renderedHeight < canvas.height) {
                var page = document.createElement("canvas");
                page.width = canvas.width;
                page.height = Math.min(imgHeight, canvas.height - renderedHeight);//可能内容不足一页

                //用getImageData剪裁指定区域，并画到前面创建的canvas对象中
                page.getContext('2d').putImageData(ctx.getImageData(0, renderedHeight, canvas.width, Math.min(imgHeight, canvas.height - renderedHeight)), 0, 0);
                pdf.addImage(page.toDataURL('image/jpeg', 1.0), 'JPEG', 10, 10, a4w, Math.min(a4h, a4w * page.height / page.width));    //添加图像到页面，保留10mm边距
                
                renderedHeight += imgHeight;
                if(renderedHeight < canvas.height)
                    pdf.addPage();//如果后面还有内容，添加一个空页
                //delete page;
            }
            pdf.save('content.pdf');
        });
  }
}
