import { Component, OnInit,Input,Output,EventEmitter} from '@angular/core';
import { SelectionModel } from '@angular/cdk/collections';
import {format} from 'date-fns';

@Component({
  selector: 'app-responses-table',
  templateUrl: './responses-table.component.html',
  styleUrls: ['./responses-table.component.css']
})
export class ResponsesTableComponent implements OnInit {
  resonses={
    // ResponseID:0,
    ResponseTypeID:0,
    // ResponseTypeName:'Accepted',
    ResponseDate:'',
    ResponseDatestr:'',
    ResponseAdditionalInfo:'',
  }
  @Input() dataSource;
  @Input() ResponseTypes;
  @Input() decisionID;
  @Output() deleteResponse = new EventEmitter();
  @Output() addResponse = new EventEmitter();
  selection  = new SelectionModel<any>(true, []);
  constructor() { }

  ngOnInit() {
  }
  responsesColumns:string[]=['select','ResponseTypeName','ResponseDateAsString','ResponseAdditionalInfo','ponit'];

  DeleteResponse(el,responses){
    this.deleteResponse.emit({data:el,table:responses,dataSource:this.dataSource});
  }

  AddResponse(responses){
    this.resonses.ResponseDate = format(this.resonses.ResponseDatestr, 'MM/DD/YYYY');
    this.addResponse.emit({param:this.resonses,decisionID:this.decisionID,dataSource:this.dataSource,table:responses});
  }

  clearVal(){
    this.resonses={
      // ResponseID:0,
      ResponseTypeID:0,
      // ResponseTypeName:'Accepted',
      ResponseDate:'',
      ResponseDatestr:'',
      ResponseAdditionalInfo:'',
    }
  }
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.length;
    return numSelected === numRows;
  }

  masterToggle() {
    this.isAllSelected() ?
        this.selection.clear() :
        this.dataSource.forEach(row => this.selection.select(row));
  }
  getKeys(item){
    if(item){
      return Object.keys(item);
    }
  }
}
