import { Component, OnInit,Renderer2 } from '@angular/core';
import { MatTableDataSource } from '@angular/material';
import { SelectionModel } from '@angular/cdk/collections';
import {animate, state, style, transition, trigger} from '@angular/animations';
import { DecisionDashboardService } from '../../service/service/decision/decision-dashboard.service'
import {format} from 'date-fns';

@Component({
  selector: 'app-my-decision',
  templateUrl: './my-decision.component.html',
  styleUrls: ['./my-decision.component.css'],
  animations: [
    trigger('detailExpand', [
      state('collapsed, void', style({ height: '0px', minHeight: '0',visibility:'hidden',padding:'0' })),
      state('expanded', style({ height: '*',padding:'14px' })),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
      transition('expanded <=> void', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)'))
    ])
  ],
})
export class MyDecisionComponent implements OnInit {
  // expandedElement;
  nextStep;
  nextStepList;
  MyDecisionTypes;
  Object = Object;
  constructor(private api :DecisionDashboardService,private renderer:Renderer2) { }
  ngOnInit() {
    // this.initTable();
    this.api.getDecisionDetails().then(res =>{
      this.nextStepList= res.NextStepDict;
      this.nextStep = '1';
      this.decisionsData = new MatTableDataSource(res.Decisions);
      this.MyDecisionTypes = res.MyDecisionTypes;
    })
  }

  decisionsColumns:string[] =['select','ProgramName','ApplicationType','AdmissionExpectation','MyDecisionAsString','NotificationDateAsString','MostRecentResponse','AnnualFinAid','TotalFinAid','arrow'];
  decisionsData:any=null;
  decisionsSelection = new SelectionModel<any>(true, []);

  editDecision(el){
    if(el.drop && !el.FinancialAidPackage){
      this.api.getEditDecision(el.DecisionID).then(res=>{
        el.Responses = new MatTableDataSource(res.Decision.Responses);
        el.FinancialAidPackage = new MatTableDataSource(res.Decision.FinancialAidPackage);
        el.ResponseTypes = res.ResponsesTypes;
        el.AwardTypes = res.AwardTypes;
      })
    }
  }

  deleteResponse(data){
    this.api.deleteResponse(data.data.ResponseID).then(res=>{
      if(res){
        alert('success');
        this.deleteTableRow(data.data.ResponseID,data.dataSource,data.table);
      }
    })
  }

  deleteAward(data){
    this.api.deleteAward(data.data.AwardID).then(res=>{
      if(res){
        alert('success');
        this.deleteTableRow(data.data.AwardID,data.dataSource,data.table);
      }
    })
  }

  addResponse(el,responsetable){
    this.api.addResponse(el.decisionID,el.param).then(res =>{
      this.addTabelRow(el.dataSource,res,el.table);
      responsetable.clearVal();
    })
  }

  addAward(el,awardstable){
    this.api.addAwards(el.decisionID,el.param).then(res =>{
      this.addTabelRow(el.dataSource,res,el.table);
      awardstable.clearVal();
    })
  }

  deleteTableRow(id,dataSource,table){
    const dsData = dataSource.data;
    const itemIndex = dsData.findIndex(obj => obj.ID == id);
    dataSource.data.splice(itemIndex, 1); 
    table.renderRows();
  }

  addTabelRow(dataSource,data,table){
    dataSource.data.push(data);
    table.renderRows();
  }

  matTableClick(data,el){
    // if(el.firstChild.style.display ==="block" || el.firstChild.style.display ===""){
    //   this.renderer.setStyle(el.lastChild,'display','block');
    //   this.renderer.setStyle(el.firstChild,'display','none');
    // }else{
    //   this.renderer.setStyle(el.lastChild,'display','none');
    //   this.renderer.setStyle(el.firstChild,'display','block');
    // }
    this.renderer.setStyle(el.lastChild,'display','block');
    this.renderer.setStyle(el.firstChild,'display','none');
  }

  selectBlur(el){
    setTimeout(()=>{
      this.renderer.setStyle(el.lastChild,'display','none');
      this.renderer.setStyle(el.firstChild,'display','block');
    },500)
  }

  //update mydecisiontype
  updateMydecision(data,el){
    this.api.QuickDecisionUpdate({DecisionID:data.DecisionID,MyDecisionTypeID:data.MyDecisionTypeID}).then(res =>{
      this.renderer.setStyle(el.lastChild,'display','none');
      this.renderer.setStyle(el.firstChild,'display','block');
      data.MyDecisionAsString = res.MyDecisionAsString;
    })
  }
//
  updateNotification(data,el){
    this.api.DecisionNotificationDate({id:data.DecisionID,date:format(data.NotificationDate, 'YYYY-MM-DD')}).then(res=>{
      this.renderer.setStyle(el.lastChild,'display','none');
      this.renderer.setStyle(el.firstChild,'display','block');
      data.NotificationDateAsString = res.NotificationDateAsString;
    })
  }

  changeNextStep(value){
    this.api.ChangeNextStep(value.key).then(res =>{
      console.log(res);
    })
  }

  quickClick(index){
    let arr= [];
    this.decisionsSelection.selected.map(item =>{
      arr.push(item.DecisionID)
    })
    this.api.QuickDecisionResponses​(index,arr).then(res=>{
      this.decisionsData.data.map(item => {
        item.MostRecentResponse = res[0].ResponseTypeName;
        item.drop =false;
        item.FinancialAidPackage = false;
      })
    })
  }

  //UpdateDecisionDetail
  saveDecisionDetail(el){
    let _param={
      "DecisionID": el.DecisionID,
      "MyDecisionTypeID": el.MyDecisionTypeID,
      "UpdatedNotificationDate": el.NotificationDate,
      "Pros": el.Pros,
      "Cons": el.Cons
    }
    this.api.updateDecisionDetail(el.DecisionID,_param).then(res =>{
      console.log(res);
    })
  }

  isAllSelected() {
    const numSelected = this.decisionsSelection.selected.length;
    const numRows = this.decisionsData.data.length;
    return numSelected === numRows;
  }

  masterToggle() {
    this.isAllSelected() ?
        this.decisionsSelection.clear() :
        this.decisionsData.data.forEach(row => this.decisionsSelection.select(row));
  }

  format(date){
    return new Date(date);
  }
}
