import { Component, OnInit,Input,Output,EventEmitter } from '@angular/core';
import { SelectionModel } from '@angular/cdk/collections';

@Component({
  selector: 'app-awards-table',
  templateUrl: './awards-table.component.html',
  styleUrls: ['./awards-table.component.css']
})
export class AwardsTableComponent implements OnInit {
  award={
    AwardTypeID:0,
    AwardName:"",
    AnnualAwardAmount:"",
    // NumofYears:"",
    TotalAwardAmount:'',
    AwardAdditionalInfo:''
  };
  @Input() dataSource;
  @Input() AwardTypes;
  @Input() decisionID;
  @Output() deleteAward = new EventEmitter();
  @Output() addAward = new EventEmitter();
  constructor() { }

  ngOnInit() {
  }
  selection  = new SelectionModel<any>(true, []);
  awardsColumns:string[]=['select','AwardTypeName','AwardName','AnnualAwardAmountAsString','NumofYears','TotalAwardAmountAsString','AwardAdditionalInfo','ponit'];
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.length;
    return numSelected === numRows;
  }

  masterToggle() {
    this.isAllSelected() ?
        this.selection.clear() :
        this.dataSource.forEach(row => this.selection.select(row));
  }

  getKeys(item){
    if(item){
      return Object.keys(item);
    }
  }

  DeleteAward(el,awardt){
    this.deleteAward.emit({data:el,table:awardt,dataSource:this.dataSource});
  }

  AddAward(awardt){
    this.addAward.emit({param:this.award,decisionID:this.decisionID,dataSource:this.dataSource,table:awardt});
  }

  clearVal(){
    this.award ={
      AwardTypeID:0,
      AwardName:"",
      AnnualAwardAmount:"",
      // NumofYears:"",
      TotalAwardAmount:'',
      AwardAdditionalInfo:''
    }
  }
}
