import { Injectable } from '@angular/core';
import { DecisionService } from '../decision.service'
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class DecisionDashboardService  extends DecisionService{

  constructor(protected http: HttpClient) { 
    super(http)
  }

  //Decision Dashboard
  public getDashBoard(){
    return new Promise<any>(resolve => {
      this.get('/decisions/dashBoard').then(res => {
        resolve(res);
      });
    });
  }

  //Decision
  public getDecisionDetails(){
    return new Promise<any>(resolve => {
      this.get('/decisions/DecisionDetails').then(res => {
        resolve(res);
      });
    });
  }
  
  public getEditDecision(id){
    return new Promise<any>(resolve => { 
      this.get(`/decisions/${id}/EditDecision`).then(res => {
        resolve(res);
      });
    });
  }

  public deleteResponse(id){
    return new Promise<any>(resolve => { 
      this.delete(`/decisions/${id}/DeleteResponse`).then(res => {
        resolve(res);
      });
    });
  }

  public addResponse(id,param){
    return new Promise<any>(resolve => { 
      this.post(`/decisions/${id}/AddDecisionResponse`,param).then(res => {
        resolve(res);
      });
    });
  }

  public addAwards(id,param){
    return new Promise<any>(resolve => { 
      this.post(`/decisions/${id}/AddAward`,param).then(res => {
        resolve(res);
      });
    });
  }

  public deleteAward(id){
    return new Promise<any>(resolve => { 
      this.delete(`/decisions/${id}/DeleteAward`).then(res => {
        resolve(res);
      });
    });
  }

  public updateDecisionDetail(id,param){
    return new Promise<any>(resolve => { 
      this.put(`/decisions/${id}/UpdateDecisionDetail`,param).then(res =>{
        resolve(res);
      })
    });
  }

  public QuickDecisionUpdate(param){
    return new Promise<any>(resolve => { 
      this.put('/decisions/QuickDecisionUpdate',param).then(res =>{
        resolve(res);
      })
    });
  }

  public DecisionNotificationDate({id,date}){
    return new Promise<any>(resolve => { 
      this.put(`/decisions/${id}/DecisionNotificationDate/${date}`).then(res =>{
        resolve(res);
      })
    });
  }

  public ChangeNextStep(id){
    return new Promise<any>(resolve => { 
      this.put(`/decisions/${id}/ChangeNextStep`).then(res =>{
        resolve(res);
      })
    });
  }

  public QuickDecisionResponses​(id,param){
    return new Promise<any>(resolve => { 
      this.post(`/decisions/QuickDecisionResponses/${id}`,param).then(res => {
        resolve(res);
      });
    });
  }

  //3
  public getProjectedCost(){
    return new Promise<any>(resolve => { 
      this.get('/decisions/ProjectedCost').then(res => {
        resolve(res);
      });
    });
  }

  public getCollegeCompareData(id){
    return new Promise<any>(resolve => { 
      this.get(`/decisions/${id}/GetCollegeCompareData`).then(res => {
        resolve(res);
      });
    });
  }
}
